import React, {useEffect} from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import Presentation from "./Presentation";
import {useParams, withRouter } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import {changeWorkAC, InitialStateTypeWork} from '../../bll/n2-reducers/works-reducer';
import { AppRootStateType } from '../../bll/n1-state/redux-state';

type Params ={
    Id:string
}
const ContainerPresentation:React.FC=(props)=> {
    let params = useParams<Params>()
    const workData= useSelector<AppRootStateType, InitialStateTypeWork>(state => state.worksReducer)
    const dispatch = useDispatch()
    useEffect(()=>{
        if(params.Id){
            dispatch(changeWorkAC(Number(params.Id)))
        }
    },[params.Id])
    console.log(workData.presentationWork)
    return (
        <Presentation href={workData.presentationWork.href}
                      img={workData.presentationWork.img}
                      description={workData.presentationWork.smallDescription}
                      name={workData.presentationWork.name}
                      GitHubLink={workData.presentationWork.GitHubLink}
        />
    );
}

export default withRouter(ContainerPresentation);