import { Dispatch } from "redux"



const initialState = {
    Language:false,

}
type ActionsType = any

export type InitialStateTypeUsers = typeof initialState


export const portfolioReducer = (state: InitialStateTypeUsers = initialState, action: ActionsType): InitialStateTypeUsers => {
    switch (action.type) {
        case 'SETUSERSCOUNT' : {
            return {...state}

        }

        default:
            return state
    }
}
