import background from "../../Images/1_vAFl1TMSGcLL24w6R5z2NQ.jpeg";
import  socialNetworkImgBack from "../../Images/Social Network lol.png"
import  toDoListImgBack from "../../Images/lokiExp.png"

type presentationWorkType ={
    id: number
    name: string
    img: string
    smallDescription: string
    href:string
    GitHubLink: string
}
type changeWork={
    type: 'CHANGE-WORK'
    id:number
}

const initialState = {
    dataWorks:[
        {id: 1, name: 'Social Network', img: socialNetworkImgBack, smallDescription: 'This is my first big project on React + Redax, here I used:\n' + '-flux concept\n' + '-Api/axios\n' + '-React Hooks\n' + '-Redux thunk\n' + '-Bootstrap\n' + '-TypeScript', href:'https://inhuman66.github.io/Social-network-new/', GitHubLink:'https://github.com/InHuman66/Social-network-new'},
        {id: 2, name: 'Todo list', img: toDoListImgBack, smallDescription: 'It`s my first project on React', href:'https://inhuman66.github.io/To-Do-List', GitHubLink:'https://github.com/InHuman66/To-Do-List' },
        {id: 3, name: 'Cards', img: background, smallDescription: 'It`s my first project on React', href:'', GitHubLink:'' },
        {id: 4, name: 'Social Network', img: background, smallDescription: 'It`s my first project on React', href:'', GitHubLink:'' },
    ],
    presentationWork:{

    }as presentationWorkType

}
type ActionsType = changeWork

export type InitialStateTypeWork = typeof initialState


export const worksReducer = (state: InitialStateTypeWork = initialState, action: ActionsType): InitialStateTypeWork => {
    switch (action.type) {
        case 'CHANGE-WORK' : {
            let newValue = {id: 1, name: 'Social Network', img: background, smallDescription: 'It`s my first project on React', href:'', GitHubLink:''}
            if(state.dataWorks.some(u => u.id === action.id)){
                // @ts-ignore
                newValue = {...state.dataWorks.find((u)=>u.id === action.id)}
            }else {
                newValue={id: 1, name: 'Social Network', img: background, smallDescription: 'It`s my first project on React', href:'',GitHubLink:'' }
            }
            return {...state, presentationWork: newValue}

        }

        default:
            return state
    }
}
export const changeWorkAC = (id: number): changeWork => {
    return {
        type: 'CHANGE-WORK',
        id: id,
    }
}
